import React, { useEffect, useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import "./homepage.scss";
import { fetchVocabularyData } from "../../fetchVocabularyData";

export default function Homepage() {
  // Startseite zeigt IMMER Turkish -> English
  const HOME_BASE = "turkish";
  const HOME_TARGET = "english";

  const navigate = useNavigate();

  const [randomVocabs, setRandomVocabs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [err, setErr] = useState(null);

  const loadRandom = async () => {
    try {
      setLoading(true);
      setErr(null);
      const data = await fetchVocabularyData(HOME_BASE, HOME_TARGET);
      const clean = data.filter(
        (i) => i[HOME_BASE] && i.translation && i.translation[HOME_TARGET]
      );
      const shuffled = [...clean].sort(() => Math.random() - 0.5);
      setRandomVocabs(shuffled.slice(0, 8));
    } catch (e) {
      setErr(e.message);
      setRandomVocabs([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadRandom();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="Homepage Main">
      {/* Hero */}
      <section className="hero">
        <div className="hero__content">
          <h1 className="hero__title">
            <span className="nowrap">
              <span className="brand">E-Langual</span>a ho geldin!
            </span>
          </h1>

          <p className="hero__subtitle">
            Bugn bala, yarn konu. ift ynl szlklerle hzl ve keyifli
            kelime ren. Arayz ve ierik artk tamamen <strong>Trke</strong>
            .
          </p>

          <div className="hero__cta">
            <NavLink to="/vocabulary" className="btn btn-primary">
              Szlkleri Kefet
            </NavLink>
            <NavLink to="/myvocabularies" className="btn btn-secondary">
              Ktphanem
            </NavLink>
          </div>

          <div className="hero__pair">
            Anasayfadaki rnekler:{" "}
            <strong>
              {HOME_BASE}  {HOME_TARGET}
            </strong>
          </div>
        </div>
      </section>

      {/* Features */}
      <section className="features">
        <div className="feature">
          <h3>Akll Arama</h3>
          <p>Hzl, toleransl arama ile aradn kelimeyi annda bul.</p>
        </div>
        <div className="feature">
          <h3>Bir Tkla Ekle</h3>
          <p>Kelimeyi Ktphaneme ve Snavlarma tek dokunula ekle.</p>
        </div>
        <div className="feature">
          <h3>Mobil Uyumlu</h3>
          <p>Telefon, tablet ve masastnde przsz deneyim.</p>
        </div>
      </section>

      {/* Random vocab list */}
      <section className="random-vocabs">
        <div className="section-header">
          <h2>te renilecek baz kelimeler</h2>
          <button className="btn btn-link" onClick={loadRandom}>
            Yenile
          </button>
        </div>

        {loading ? (
          <div className="skeleton-grid" aria-label="ykleniyor">
            {Array.from({ length: 8 }).map((_, i) => (
              <div className="skeleton-card" key={i} />
            ))}
          </div>
        ) : err ? (
          <div className="error-box">Bir eyler ters gitti: {err}</div>
        ) : (
          <div className="vocab-grid">
            {randomVocabs.map((item, idx) => (
              <button
                type="button"
                className="vocab-card"
                key={idx}
                onClick={() => navigate("/vocabulary")}
                title="Szlklere git"
              >
                <div className="vocab-card__base">{item[HOME_BASE]}</div>
                <div className="vocab-card__arrow"></div>
                <div className="vocab-card__target">
                  {item.translation[HOME_TARGET]}
                  {item.pronunciation && HOME_TARGET === "spanish" && (
                    <span className="vocab-card__pron">
                      {" "}
                      ({item.pronunciation})
                    </span>
                  )}
                </div>
              </button>
            ))}
          </div>
        )}

        <div className="section-cta">
          <NavLink to="/vocabulary" className="btn btn-primary">
            Tm kelimeleri gr
          </NavLink>
        </div>
      </section>

      {/* Bottom CTA */}
      <section className="cta-banner">
        <h3>renmeyi alkanla evir.</h3>
        <p>Her gn 10 yeni kelime ekle, haftada bir mini snav z!</p>
        <div className="cta-banner__actions">
          <NavLink to="/myexams" className="btn btn-secondary">
            Snavlarm
          </NavLink>
          <NavLink to="/grammar" className="btn btn-ghost">
            Dilbilgisi
          </NavLink>
        </div>
      </section>
    </div>
  );
}
